.learning {
  overflow: hidden;
}

.learning__wrapper {
  margin: 0 auto;
  max-width: 1260px;

  @include vp-1199 {
    max-width: 738px;
    overflow-x: hidden;
  }
}

.learning__title {
  padding: 0 30px;
  padding-top: 15px;
  margin-bottom: 50px;

  @include vp-1199 {
    padding-top: 0;
    margin-bottom: 55px;
  }

  @include vp-767 {
    padding: 0 15px;
    margin-bottom: 37px;
  }
}

.learning__title h2 {
  position: relative;
  margin-bottom: 30px;

  @include vp-1199 {
    margin-bottom: 20px;
  }

  @include vp-767 {
    margin-bottom: 18px;
  }
}

.learning__title p {
  margin: 0;
  padding: 0;

  max-width: 820px;

  font-size: 16px;
  line-height: 24px;

  @include vp-1199 {
    max-width: 560px;
  }

  @include vp-767 {
    max-width: 290px;

    font-size: 14px;
    line-height: 20px;
  }
}

.learning__instructors-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;

  @include vp-767 {
    padding: 0 15px;
    margin-bottom: 10px;
  }
}

.learning__instructors-wrapper h3 {
  margin: 0;

  font-size: 21px;
  line-height: 21px;

  @include vp-1199 {
    font-size: 18px;
    line-height: 18px;
  }

  @include vp-767 {
    font-size: 16px;
    line-height: 16px;
  }
}

.learning__slider-buttons {
  display: flex;
  gap: 6px;
}

.learning__instructors-slider {
  padding: 0 30px;
  padding-bottom: 50px;
  padding-top: 35px;
  overflow: hidden;

  @include vp-1199 {
    padding-top: 25px;
    padding-bottom: 45px;
  }

  @include vp-767 {
    padding: 15px;
    margin-bottom: 25px;
  }
}

.learning__instructors-slider-slide {
  position: relative;
  width: 278px;
  height: auto;
  min-height: 377px;
  padding: 20px;

  background-color: $color-default-white;


  @include vp-1199 {
    width: 206px;
    padding: 15px;
  }

  @include vp-767 {
    width: 290px;
    padding: 20px;
    margin: 0 auto;
  }
}

.learning__instructors-slider-slide::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  box-shadow: 0 0 30px 0 rgba(28, 51, 116, 0.1);
}

.learning__about {
  padding-bottom: 80px;

  @include vp-1199 {
    padding-bottom: 38px;
    margin: 0 auto;
    max-width: 678px;
  }

  @include vp-767 {
    padding-bottom: 15px;
  }
}

.learning__about h3 {
  margin: 0;
  margin-bottom: 43px;

  font-size: 21px;
  line-height: 21px;
  font-weight: 700;

  @include vp-1199 {
    margin-bottom: 40px;

    font-size: 18px;
    line-height: 18px;
  }

  @include vp-767 {
    margin-bottom: 30px;

    font-size: 16px;
    line-height: 16px;
  }
}

.learning__about-list {
  position: relative;
  margin: 0;
  padding: 0;
  padding-bottom: 32px;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  column-gap: 32px;

  @include vp-1199 {
    grid-template-columns: 1fr 1fr;
    padding-bottom: 0;
    row-gap: 87px;
    column-gap: 30px;
  }

  @include vp-767 {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin: 0 auto;
    max-width: 450px;
  }
}

.learning__about-list::after {
  content: "";
  position: absolute;
  width: 1000%;
  height: 1px;
  opacity: 0.2;
  background: #0266c1;
  bottom: 0;
  left: -1000px;

  @include vp-1199 {
    display: none;
  }
}

.learning__about-list-item {
  position: relative;
  display: flex;
  align-items: center;
  gap: 15px;
  min-height: 48px;
  padding-left: 40px;

  @include vp-767 {
    display: block;
    padding-left: 65px;
    padding-bottom: 14px;
    min-height: 44px;
  }
}


.learning__about-list-item svg {
  position: absolute;
  bottom: 14px;
  left: 0;

  @include vp-767 {
    width: 19px;
    height: 19px;
    bottom: 50%;
    left: 34px;
  }
}

.learning__about-list-item:first-child::after {
  @include vp-1199 {
    content: "";
    position: absolute;
    width: 110%;
    height: 1px;
    background: #0266c1;
    opacity: 0.2;
    top: 120%;
    max-width: 570px;
    left: 0;
  }

  @include vp-767 {
    content: "";
    position: absolute;
    width: 1px;
    height: 100%;
    background: #0266c1;
    opacity: 0.2;
    left: 10px;
    top: 25px;
  }
}

.learning__about-list-item:last-child::after {
  @include vp-1199 {
    content: "";
    position: absolute;
    width: 110%;
    height: 1px;
    background: #0266c1;
    opacity: 0.2;
    top: -15px;
    left: 0;
  }

  @include vp-767 {
    display: none;
  }
}

.learning__about-list-item:nth-child(2n):not(:nth-child(4n))::after {
  @include vp-1199 {
    content: "";
    position: absolute;
    width: 1px;
    height: 56px;
    background: #0266c1;
    opacity: 0.2;
    left: 5px;
    bottom: -73px;
  }

  @include vp-767 {
    content: "";
    position: absolute;
    width: 1px;
    height: 100%;
    background: #0266c1;
    opacity: 0.2;
    left: 10px;
    top: 25px;
  }
}

.learning__about-list-item:nth-child(3n)::after {
  @include vp-767 {
    content: "";
    position: absolute;
    width: 1px;
    height: 100%;
    background: #0266c1;
    opacity: 0.2;
    left: 10px;
    top: 25px;
  }
}


.learning__about-list-item::before {
  content: "";
  position: absolute;
  width: 14px;
  height: 14px;
  bottom: -37px;
  left: 0;
  border-radius: 50%;
  background-color: $color-additional-icon;

  @include vp-1199 {
    bottom: -35%;
    left: 0;
  }

  @include vp-767 {
    left: 4px;
    bottom: auto;
    top: 10px;
  }
}

.learning__about-list-item:nth-child(3n)::before,
.learning__about-list-item:nth-child(4n)::before {
  @include vp-1199 {
    bottom: auto;
    top: -25px;
  }

  @include vp-767 {
    top: 10px;
  }
}

.learning__about-list-item:last-child {
  margin-right: 0;
  padding-bottom: 0;

  @include vp-1199 {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}

.learning__about-list-item span {
  display: block;

  font-size: 16px;
  line-height: 24px;

  @include vp-1199 {
    font-size: 14px;
    line-height: 22px;
    padding-right: 50px;
  }

  @include vp-767 {
    font-size: 12px;
    line-height: 18px;
  }
}
